.body {
    background-color: black;
}

.logo {
    max-width: 100px;
}
.img-top {
    max-width: 350px;
}

.insta {
    font-size: 1.5rem;
    font-weight: 600;
}

.carousel-inner {
    width: 80%;
    margin: 0 auto;
}

.carousel-item {
    height: 800px;
}

.carousel-item img {
    object-fit: cover;
    height: 100%;
}

.carousel-indicators li {
    width: 15px;
    height: 15px;
    margin-right: 5px;
    margin-left: 5px;
}

.carousel-indicators .active {
    width: 19px;
    height: 19px;
}





